<template>
  <div>
    <el-card class="header">
      <el-form :inline="true" class="headerForm" :model="searchHeaderForm">
        <el-form-item
          ><el-input
            clearable
            v-model="searchHeaderForm.find"
            placeholder="请输入邮箱/名称/电话"
          ></el-input>
        </el-form-item>
        <el-button type="primary" @click="getDatas">查询</el-button>
        <el-button class="searchBtn" type="primary" @click="handleData('add')"
          >添 加</el-button
        >
      </el-form>
    </el-card>
    <el-card>
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        :header-cell-style="{ background: '#F3F4F7', color: '#555' }"
      >
        <el-table-column prop="email" label="邮箱" min-width="200">
        </el-table-column>
        <el-table-column prop="name" label="姓名" min-width="200">
        </el-table-column>
        <el-table-column prop="phone" label="电话" min-width="200">
        </el-table-column>
        <el-table-column prop="create_time" label="创建时间" min-width="200">
        </el-table-column>
        <el-table-column align="center" label="密码" min-width="200">
          <template #default="scope">
            <el-button type="primary" @click="resetPassword(scope.row._id)"
              >重置密码</el-button
            >
          </template>
        </el-table-column>
        <el-table-column align="center" fixed="right" label="操作" width="200">
          <template #default="scope">
            <el-button type="primary" @click="handleData('update', scope.row)"
              >修改</el-button
            >
            <el-button
              type="danger"
              :disabled="scope.row._id == userID"
              @click="deleteData(scope.row._id)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <div
        v-if="tableData.length !== 0"
        style="display: flex; flex-direction: row-reverse; margin-top: 10px"
      >
        <el-pagination
          :current-page="currentPage"
          :page-size="pageSize"
          :total="total"
          :page-sizes="[10, 20, 50, 100]"
          background
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        />
      </div>
    </el-card>
    <el-dialog
      :modal-append-to-body="false"
      v-model="operaDialog"
      :title="dialogTitle"
      @close="resetForm"
    >
      <el-form
        status-icon
        :model="operaForm"
        ref="operaForm"
        class="operaDialog"
        label-width="100px"
        :rules="rules"
      >
        <el-form-item label="姓名" prop="name">
          <el-input v-model="operaForm.name" />
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="operaForm.email" />
        </el-form-item>
        <el-form-item label="电话" prop="phone">
          <el-input v-model="operaForm.phone" />
        </el-form-item>
        <el-form-item label="密码" prop="password" v-if="isupdate">
          <el-input
            v-model="operaForm.password"
            type="password"
            placeholder="请输入密码"
            show-password
          />
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm()">提交</el-button>
          <el-button @click="resetForm">取消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
import {
  findManager,
  createManager,
  deleteManager,
  updateManager,
  manager_reset_password,
} from "@/api/manager/api.js";
// eslint-disable-next-line no-unused-vars
export default {
  data() {
    return {
      userID: sessionStorage.getItem("user_id"),
      tableData: [],
      // 控制收缩
      isCollapse: false,
      isupdate: false,
      search: "",
      operaDialog: false,
      dialogTitle: "",
      pageSize: 10, // 分页的页面大小
      total: 0, // 分页的数据总数
      currentPage: 1, // 分页的当前页
      opera: "",
      operaForm: {
        name: "",
        phone: "",
        email: "",
        password: "",
      },
      searchHeaderForm: {
        //查询
        find: "",
      },
    };
  },
  created() {
    this.getDatas();
  },
  methods: {
    desearch() {
      this.search = "";
      this.getDatas();
    },
    handleData(handle, row) {
      if (handle == "add") {
        this.operaForm = {};
        this.dialogTitle = "添加API";
        this.isupdate = true;
        this.opera = "add";
      } else if (handle == "update") {
        this.operaForm = { ...row }; //!!! 直接赋值是引用类型,引起表单重置问题
        this.dialogTitle = "编辑API";
        this.isupdate = false;
        this.opera = "update";
      }
      console.log(handle);
      this.operaDialog = true;
      console.log(this.operaDialog);
    },
    handleSizeChange(value) {
      // 改变页面大小
      this.pageSize = value;
      this.getDatas();
    },
    handleCurrentChange(value) {
      // 改变页数
      this.currentPage = value;
      this.getDatas();
    },
    async getDatas() {
      const params = {
        page: this.currentPage,
        page_size: this.pageSize,
        search: this.searchHeaderForm.find,
      };
      const res = await findManager(params);
      if (res.data.code === 200) {
        if (res.data.data) {
          this.total = res.data.data.count;
          this.tableData = res.data.data.data;
          if (res.data.data.data == null) {
            this.tableData = [];
          }
        } else {
          this.total = res.data.data.count;
          this.tableData = [];
        }
      }
    },
    addData() {
      createManager(this.operaForm).then((res) => {
        console.log(res);
        if (res.data.code === 200) {
          this.$message({
            showClose: true,
            message: "创建成功",
            type: "success",
          });
          this.operaDialog = false;
          this.getDatas();
        } else {
          this.$message({
            showClose: true,
            message: res.data.msg,
            type: "error",
          });
        }
      });
    },
    deleteData(data) {
      deleteManager({ _id: data }).then((res) => {
        console.log(data);
        if (res.data.code === 200) {
          this.$message({
            showClose: true,
            message: "删除成功",
            type: "success",
          });
          this.operaDialog = false;
          this.getDatas();
        }
      });
    },
    editData() {
      updateApi(this.operaForm).then((res) => {
        if (res.data.code === 200) {
          this.$message({
            showClose: true,
            message: res.data.msg,
            type: "success",
          });
          this.operaDialog = false;
          this.getDatas();
        }
      });
    },

    submitForm() {
      this.$refs.operaForm.validate((valid) => {
        if (valid) {
          if (this.opera === "add") {
            this.addData();
          } else {
            this.editData();
          }
        } else {
          this.$message({
            showClose: true,
            message: "请根据要求将信息填写完整",
            type: "warning",
          });
        }
      });
    },
    resetForm() {
      this.$refs.operaForm.clearValidate();
      (this.operaForm = {}), (this.operaDialog = false);
    },
    resetPassword(id) {
      manager_reset_password({ _id: id }).then((res) => {
        if (res.data.code === 200) {
          this.$message({
            showClose: true,
            message: "重置密码成功",
            type: "success",
          });
        } else {
          this.$message({
            showClose: true,
            message: res.data.msg,
            type: "error",
          });
        }
      });
    },
  },
};
</script>
  
<style lang="less" scoped>
.header {
  margin-bottom: 20px;

  .searchBtn {
    float: right;
  }

  .headerForm {
    padding: 0 !important;

    .el-form-item {
      margin-bottom: 0px;
    }
  }
}

.upload-demo {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}

.el-date-editor.el-input {
  width: 100%;
}

.fileLink {
  color: #f8f8f8;
  text-decoration: none;
}

.el-form {
  padding: 0 100px;
}

/deep/ .operaDialog .el-form-item__label {
  padding: 0 25px 0 0;
}
</style>
  